<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

// 表單驗證
const checkSpecialKey = (str) => {
  let specialKey = /[!@#$%^&*()_+]/;
  return !specialKey.test(str);
};

export default {
  name: "constructionGroupSettingComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    // 表單驗證
    let validateSpecialCharacters = (rule, value, callback) => {
      if (!checkSpecialKey(value)) {
        callback(new Error("不合法特殊字元[ ' &%]存在，請移除"));
      } else {
        callback();
      }
    };
    return {
      selectedData: null,
      formData: [],
      form: {
        group_name: "",
        workGroupList: [],
      },
      rules: {
        group_name: [
          { required: true, message: "必填", trigger: "blur" },
          { max: 10, message: "最多10個字元", trigger: "blur" },
          { validator: validateSpecialCharacters, trigger: "blur" },
        ],
        workGroupList: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "group_Name",
          label: "工作群組名稱",
          width: "250",
        },
        {
          id: "groupPeople",
          label: "人員清單與安全衛生人員",
        },
      ],
      head: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    setForm() {
      this.form.workGroupList.forEach((e) => {
        const data = {
          GROUP_NAME: this.form.group_name,
          ID: e,
          S_ID: this.getSid(e),
        };
        this.formData.push(data);
      });
    },
    changeSelection(item) {
      const data = {
        Groups: [],
      };
      data.Groups.push(item);
      data.Groups = data.Groups.flat();
      this.selectedData = data;
    },
    async confirmHandler() {
      try {
        const res = await this.$store.dispatch(
          "delGroupList",
          this.selectedData
        );
        if (res.data.isOK) {
          this.getGroupList();
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "刪除成功",
          });
          this.$refs.multipleTable.$refs.multipleTable.clearSelection();
          this.$store.state.currentPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    getSid(id) {
      const sid = this.head.find((e) => e.id === id).s_Id;
      return sid;
    },
    // API取得資料
    async getGroupList() {
      try {
        const res = await this.$store.dispatch("getGroupList");
        const data = res.data.data;
        this.head = data.head;
        this.head.forEach((e) => (e.label = `${e.p_Name}(${e.id})`));
        this.tableData = JSON.parse(JSON.stringify(data.list));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          const person = e.person_List.map((x) => x.p_Name);
          const newPerson = person.filter((z) => z !== null && z !== undefined);
          e.groupPeople = newPerson;
          e.groupPeople = e.groupPeople.join();
        });
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      this.setForm();
      // console.log(this.formData);
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const res = await this.$store.dispatch("insGroup", this.formData);
            if (res.data.isOK) {
              this.getGroupList();
              this.resetForm(formName);
              this.$message({
                type: "success",
                duration: 5000,
                showClose: true,
                message: "成功",
              });
              this.$refs.multipleTable.$refs.multipleTable.clearSelection();
              this.$store.state.currentPage = 1;
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
  },
  mounted() {
    this.$store.state.currentPage = 1;
    this.getGroupList();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./constructionGroupSetting.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
